.sidebar {
    box-sizing: border-box;
}

.profile {
    padding-top: 5px;
    position: absolute;
    padding-left: 5px;
    left: 0px;
    top: 0vw;
    width: 7.5vw;
    height: 7.5vw;
    bottom: 8vw;
}

h1{
    font-size: 0.85vw; 
    color: whitesmoke;
    background-color: black;
    text-align: center;
    position: absolute;
    left: 5px;
    top: 6.8vw;
    width: 7.5vw;
    height: 1.5vw;
}

.social{
    position:absolute;
    left: 7px;
    top: 35vw;
    font-size: 2vw;
    transition: top 2.5s;
}

i{
    padding-left: 1.5vw;
    padding-top: 1.5vw;
}

a:link{
    color: black;
}

a:visited {
    color: maroon;
}

